import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import ReceivablePaymentCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/receivablePayment/actions";
import cashBankActions from "../../../../redux/cashBank/cashBank/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/financials/receivable-payments";
const { storeCashBank } = cashBankActions;

class CreateReceivablePayment extends Component {
  state = {
    visibleAddCashBank: false
  };

  componentDidMount() {
    const { createReceivablePayment } = this.props;
    createReceivablePayment();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.receivablePayment.error) {
      notification("error", this.props.receivablePayment.error);
      this.props.clearError();
    }
    if (
      (this.props.receivablePayment.success ||
        this.props.receivablePayment.warning) &&
      this.props.receivablePayment.id_receivable_payment
    ) {
      this.props.history.replace(
        redirecTo +
          "/update/" +
          this.props.receivablePayment.id_receivable_payment
      );
      // this.props.history.replace(redirecTo);
      // notification("success", this.props.receivablePayment.success);
      // this.props.clearSuccess();
    }

    // Cash Bank
    if (this.props.cashBank.error) {
      notification("error", this.props.cashBank.error);
      this.props.clearError();
    }

    if (this.props.cashBank.success) {
      const { createReceivablePayment, clearSuccess } = this.props;
      notification("success", this.props.cashBank.success);
      this.handleModalAddCashBank();
      clearSuccess();
      createReceivablePayment();
    }
  }

  handleModalAddCashBank = () => {
    this.setState({
      visibleAddCashBank: !this.state.visibleAddCashBank
    });
  };

  handleSubmit = value => {
    const { storeReceivablePayment } = this.props;
    storeReceivablePayment(value);
  };

  handleSubmitCashBank = value => {
    const { storeCashBank } = this.props;
    storeCashBank(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="receivablePayments.button.add" />}
          >
            <Button
              onClick={() => {
                removeSaveData("rp");
                this.props.history.replace(redirecTo);
              }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <ReceivablePaymentCreateForm
            submit={this.handleSubmit}
            receivablePayment={this.props.receivablePayment}
            // Cash Bank
            cashBank={this.props.cashBank}
            visibleAddCashBank={this.state.visibleAddCashBank}
            handleModalAddCashBank={this.handleModalAddCashBank}
            handleSubmitCashBank={this.handleSubmitCashBank}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    receivablePayment: state.receivablePayment,
    cashBank: state.cashBank
  };
};

export default compose(connect(mapStateToProps, { ...actions, storeCashBank }))(
  CreateReceivablePayment
);
