import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import { Form, InputNumber, Button, Select, Col, Row, Modal } from "antd";

const { Option } = Select;

class ModalDiscount extends Component {
  state = {
    err: null
  };
  componentDidUpdate(prevProps) {
    const { selectedData } = this.props;
    const { setFieldsValue } = this.props.form;

    if (prevProps.selectedData !== selectedData) {
      setFieldsValue({
        discount_amount: Array.isArray(selectedData.detail_discounts)
          ? selectedData.detail_discounts[0].discount_amount
            ? selectedData.detail_discounts[0].discount_amount
            : null
          : null,
        // id_account_discount: Array.isArray(selectedData.detail_discounts)
        //   ? selectedData.detail_discounts[0].id_account_discount
        //     ? selectedData.detail_discounts[0].id_account_discount
        //     : null
        //   : null,
        id_department: Array.isArray(selectedData.detail_discounts)
          ? selectedData.detail_discounts[0].id_department
            ? selectedData.detail_discounts[0].id_department
            : null
          : null,
        id_project: Array.isArray(selectedData.detail_discounts)
          ? selectedData.detail_discounts[0].id_project
            ? selectedData.detail_discounts[0].id_project
            : null
          : null
      });
    }
  }
  handleSubmit = () => {
    // e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      const { selectedData } = this.props;
      let left_amount =
        selectedData.sales_invoice_amount -
        selectedData.paid_amount -
        selectedData.sub_total_memo_amount;

      if (
        parseFloat(parseFloat(values.discount_amount).toFixed(2)) > 
        parseFloat(left_amount.toFixed(2))
      ) {
        this.setState({
          err: <IntlMessages id="receivablePayments.discountExceeds" />
        });
        return;
      }

      if (!err) {
        const { selectedData } = this.props;

        values.id_department = values.id_department ? values.id_department : null;
        values.id_project = values.id_project ? values.id_project : null;
        
        const discounts = {
          detail_discounts: [{ ...values, id_account_discount: null }]
        };

        const data = Object.assign({}, selectedData, discounts);
        this.setState({ err: null });
        // console.log(data);
        this.props.submit(data);
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();

    const { selectedData } = this.props;
    const discounts = {
      detail_discounts: [
        {
          id_account_discount: null,
          id_department: null,
          id_project: null,
          discount_amount: 0
        }
      ]
    };

    const data = Object.assign({}, selectedData, discounts);
    this.setState({ err: null });
    // console.log(data);
    this.props.submit(data);
  };

  render() {
    // console.log(this.props);
    const { err } = this.state;
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { departments, projects } = this.props;
    // const error = pettyCashType.errorField ? pettyCashType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="receivablePayments.discount" />}
        visible={this.props.visible}
        keyboard={false}
        maskClosable={false}
        centered
        onCancel={this.props.handleDiscountModal}
        width="50%"
        footer={
          <Row justify="end" type="flex">
            <Col>
              <Button
                onClick={() => {
                  this.props.handleDiscountModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>

              <Button
                onClick={() => {
                  this.handleReset();
                }}
                disabled={this.props.status !== 1}
              >
                <IntlMessages id="filter.reset" />
              </Button>

              <Button
                type="primary"
                onClick={() => {
                  this.handleSubmit();
                }}
                disabled={this.props.status !== 1}
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="receivablePayments.discountAmount" />}
                validateStatus={
                  err || getFieldError("discount_amount") ? "error" : null
                }
                help={err ? err : getFieldError("discount_amount")}
              >
                {getFieldDecorator("discount_amount", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <InputNumber
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    min={0.00}
                    precision={2}
                    style={{ width: "100%" }}
                    disabled={this.props.status !== 1}
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
          {/* <Row>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="receivablePayments.GlAccount" />}
              >
                {getFieldDecorator("id_account_discount", {
                  // initialValue: data.id_account_discount,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    style={{ width: "100%" }}
                    placeholder={
                      <IntlMessages id="receivablePayments.GlAccount" />
                    }
                    optionFilterProp="children"
                    filterOption={true}
                  >
                    {accounts.map((data, key) => (
                      <Option key={key} value={data.id_account}>
                        {`${data.account_code} ${data.account_name}`}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row> */}
          <Row type="flex" justify="start" gutter={6}>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="receivablePayments.department" />}
              >
                {getFieldDecorator("id_department", {
                  //   initialValue: selectedData.id_department
                })(
                  <Select
                    allowClear
                    placeholder={
                      <IntlMessages id="receivablePayments.department" />
                    }
                    disabled={this.props.status !== 1}
                    optionFilterProp="children"
                    filterOption={true}
                  >
                    {departments.map((data, key) => (
                      <Option key={key} value={data.id_department}>
                        {data.department_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="receivablePayments.project" />}
              >
                {getFieldDecorator("id_project", {
                  //   initialValue: selectedData.id_project
                })(
                  <Select
                    allowClear
                    placeholder={
                      <IntlMessages id="receivablePayments.project" />
                    }
                    disabled={this.props.status !== 1}
                    optionFilterProp="children"
                    filterOption={true}
                  >
                    {projects.map((data, key) => (
                      <Option key={key} value={data.id_project}>
                        {data.project_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedDiscountModal = Form.create({ name: "discount_modal" })(
  ModalDiscount
);

export default WrappedDiscountModal;
