const actions = {
  getReceivablePayments: (page, search, filter) => ({
    type: "GET_RECEIVABLE_PAYMENTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createReceivablePayment: () => ({
    type: "CREATE_RECEIVABLE_PAYMENT_REQUEST"
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_FILTER_SALE_INVOICES_RP_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getCreditMemos: (id_location, id_customer) => ({
    type: "GET_FILTER_CREDIT_MEMOS_RP_REQUEST",
    payload: {
      id_location: id_location,
      id_customer: id_customer
    }
  }),
  storeReceivablePayment: data => ({
    type: "STORE_RECEIVABLE_PAYMENT_REQUEST",
    payload: { body: data }
  }),
  editReceivablePayment: (id_receivable_payment, search) => ({
    type: "EDIT_RECEIVABLE_PAYMENT_REQUEST",
    payload: { id_receivable_payment: id_receivable_payment, search: search }
  }),
  updateReceivablePayment: (id_receivable_payment, data) => ({
    type: "UPDATE_RECEIVABLE_PAYMENT_REQUEST",
    payload: {
      id_receivable_payment: id_receivable_payment,
      body: data
    }
  }),
  showReceivablePayment: id_receivable_payment => ({
    type: "SHOW_RECEIVABLE_PAYMENT_REQUEST",
    payload: { id_receivable_payment: id_receivable_payment }
  }),
  confirmReceivablePayment: id_receivable_payment => ({
    type: "CONFIRM_RECEIVABLE_PAYMENT_REQUEST",
    payload: { id_receivable_payment }
  }),
  cancelReceivablePayment: (id_receivable_payment, data) => ({
    type: "CANCEL_RECEIVABLE_PAYMENT_REQUEST",
    payload: { id_receivable_payment: id_receivable_payment, body: data }
  }),
  uploadDocument: (id_receivable_payment, data) => ({
    type: "UPLOAD_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST",
    payload: {
      id_receivable_payment,
      body: data
    }
  }),
  downloadDocument: id_receivable_payment_document => ({
    type: "DOWNLOAD_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST",
    payload: { id_receivable_payment_document }
  }),
  deleteDocument: (id_receivable_payment_document, id_receivable_payment) => ({
    type: "DELETE_RECEIVABLE_PAYMENT_DOCUMENT_REQUEST",
    payload: {
      id_receivable_payment_document: id_receivable_payment_document,
      id_receivable_payment: id_receivable_payment
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_RECEIVABLE_PAYMENT",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
