import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/financial/receivablePayment/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  InputNumber,
  DatePicker,
  Select,
  Row,
  Col,
  Tabs,
  Empty,
  Tooltip,
  Icon,
  Divider
} from "antd";
import notification from "../../../../components/notification";
import EditableTable from "./editableTable";
import ModalTransaction from "./modalSaleInvoice";
// import { Object } from "core-js";
import {
  removeSaveData,
  // getCurrencyRate,
  getRate
} from "../../../../helpers/helper";
import getLanguage from "../../../App/MyLanguange";
import ModalAddCashBank from "./modalAddCashBank";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;
const TabPane = Tabs.TabPane;

const { getTransactions, getDiscounts } = actions;

class ReceivablePaymentCreateForm extends Component {
  state = {
    id_location: null,
    id_customer: null,
    invoice: null,
    invoices: [],
    invoiceDetails: {
      receivable_payment_details: []
    },
    multiPaymentDetails: {
      receivable_payment_categories: []
    },
    currency_code: null,
    currency_rate: 1,
    total_payment_amount: 0,
    total_debit_memo: 0,
    total_discount: 0,
    search: null,
    visible: false,
    visibleCreditMemo: false,
    err: false,
    err_invoice: false,
    errPaymentList: false,
    restore: null,
    clear: false
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      const {
        invoiceDetails,
        multiPaymentDetails,
        total_payment_amount,
        total_debit_memo,
        total_discount
      } = this.state;

      const { messages } = this.props.receivablePayment;

      // validasi invoice
      if (invoiceDetails.receivable_payment_details.length <= 0) {
        return this.setState({ err_invoice: true });
      } else {
        this.setState({ err_invoice: false });
      }

      if (err) {
        this.setState({ err: true });
        return;
      }

      values.transaction_date = values.transaction_date.format(
        "YYYY-MM-DD HH:mm:ss"
      );
      values.due_date = values.due_date
        ? values.due_date.format("YYYY-MM-DD HH:mm:ss")
        : null;
      values.customer_credit_limit = values.customer_credit_limit
        ? parseFloat(values.customer_credit_limit.replace(/\$\s?|(,*)/g, ""))
        : 0;

      const data = Object.assign(
        {},
        values,
        invoiceDetails,
        multiPaymentDetails,
        {
          total_payment_amount: total_payment_amount,
          total_amount: total_payment_amount + total_debit_memo + total_discount
        }
      );

      if (invoiceDetails.receivable_payment_details.length < 1) {
        notification("error", messages.invoice_required);
      } else if (
        multiPaymentDetails.receivable_payment_categories.length === 0 &&
        total_payment_amount !== 0
      ) {
        this.setState({ err: true, errPaymentList: true });
        notification("error", messages.payment_category_required);
      } else if (!err) {
        this.setState({ err: false, errPaymentList: false });
        this.props.submit(data);
      }
    });
  };

  handleChangeTransDate = status => {
    if (!status) {
      const { getFieldValue, getFieldsValue, setFieldsValue } = this.props.form;
      const { currencies } = this.props.receivablePayment;

      let currRate = 1;
      let fiscRate = 1;
      if (getFieldValue("id_currency")) {
        const currency = currencies.find(
          data => data.id_currency === getFieldValue("id_currency")
        );
        const rate = getRate(
          currency.currency_rate,
          getFieldValue("transaction_date")
        );
        currRate = rate.currency_rate;
        fiscRate = rate.fiscal_rate;
      }

      this.setState({ currency_rate: currRate });
      setFieldsValue({
        currency_rate: currRate ? currRate : 1,
        fiscal_rate: fiscRate ? fiscRate : 1
      });

      const { getTransactions } = this.props;

      let { transaction_date, id_location, id_customer } = getFieldsValue([
        "transaction_date",
        "id_location",
        "id_customer"
      ]);
      transaction_date = moment(transaction_date.format("YYYY-MM-DD HH:mm:ss"))
        .endOf("day")
        .format("YYYY-MM-DD HH:mm:ss");

      if (id_location && id_customer) {
        const filterInvoice = `id_customer=${id_customer}&id_location=${id_location}&transaction_date=${transaction_date}`;
        getTransactions(1, null, filterInvoice);
      }
    }
  };

  handleModal = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSelectLocation = id_location => {
    const { setFieldsValue } = this.props.form;
    this.setState({
      id_location: id_location,
      clear: true
    });

    setFieldsValue({
      id_cash_bank: null
    });
  };

  handleSelectModal = value => {
    const { transactions } = this.props.receivablePayment;
    const { setFieldsValue } = this.props.form;
    const transaction = transactions.find(
      curr_data => curr_data.id_sales_invoice === value
    );

    setFieldsValue({
      id_location: transaction.id_location,
      id_customer: transaction.id_customer
    });

    this.setState({
      invoice: transaction
    });

    this.handleModal();
  };

  handleChangeCustomer = id_customer => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { currencies, customers } = this.props.receivablePayment;
    const customer = customers.find(data => data.id_customer === id_customer);
    const currency = currencies.find(
      data => data.id_currency === customer.id_currency
    );

    const rate = getRate(
      currency.currency_rate,
      getFieldValue("transaction_date")
    );
    const currRate = rate.currency_rate;
    const fiscRate = rate.fiscal_rate;

    this.setState({
      id_customer: id_customer,
      currency_rate: currRate,
      currency_code: currency.currency_code,
      clear: true
    });

    setFieldsValue({
      customer_credit_limit: customer
        ? parseFloat(customer.customer_credit_limit)
            .toFixed(2)
            .replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        : 0,
      id_currency: customer ? customer.id_currency : null,
      currency_rate: currRate ? currRate : 1,
      fiscal_rate: fiscRate ? fiscRate : 1
    });
  };

  handleSaveInvoiceList = (
    invoices,
    total_payment_amount,
    total_debit_memo,
    total_discount
  ) => {
    // untuk sementara
    const multiPaymentDetails = {
      receivable_payment_categories: [
        {
          index_no: 1,
          id_payment_category: null,
          payment_type: null,
          payment_amount: total_payment_amount,
          total_amount: total_payment_amount,
          bank_issuer: null,
          reference_no: null,
          card_no: null,
          name_on_card: null
        }
      ]
    };

    this.setState({
      invoiceDetails: { receivable_payment_details: invoices },
      multiPaymentDetails,
      total_payment_amount: total_payment_amount,
      total_debit_memo: total_debit_memo,
      total_discount: total_discount,
      clear: false
    });
  };

  handleSavePaymentList = value => {
    const { receivable_payment_categories } = value;
    this.setState({
      multiPaymentDetails: { receivable_payment_categories }
    });
  };

  clearInvoice = (
    invoices,
    total_payment_amount,
    total_debit_memo,
    total_discount
  ) => {
    // untuk sementara
    const multiPaymentDetails = {
      receivable_payment_categories: [
        {
          index_no: 1,
          id_payment_category: null,
          payment_type: null,
          payment_amount: total_payment_amount,
          total_amount: total_payment_amount,
          bank_issuer: null,
          reference_no: null,
          card_no: null,
          name_on_card: null
        }
      ]
    };

    this.setState({
      invoice: null,
      invoiceDetails: { receivable_payment_details: invoices },
      multiPaymentDetails,
      total_payment_amount: total_payment_amount,
      total_debit_memo: total_debit_memo,
      total_discount: total_discount
    });
  };

  clearInvoices = (
    invoices,
    total_payment_amount,
    total_debit_memo,
    total_discount
  ) => {
    // untuk sementara
    const multiPaymentDetails = {
      receivable_payment_categories: [
        {
          index_no: 1,
          id_payment_category: null,
          payment_type: null,
          payment_amount: total_payment_amount,
          total_amount: total_payment_amount,
          bank_issuer: null,
          reference_no: null,
          card_no: null,
          name_on_card: null
        }
      ]
    };

    this.setState({
      invoices: [],
      invoiceDetails: { receivable_payment_details: invoices },
      multiPaymentDetails,
      total_payment_amount: total_payment_amount,
      total_debit_memo: total_debit_memo,
      total_discount: total_discount,
      clear: false
    });
  };

  render() {
    // console.log(this.props);
    const { visible } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;

    const { receivablePayment } = this.props;
    const {
      locations,
      customers,
      currencies,
      accounts,
      cashBanks,
      cashFlowTypes,
      paymentCategories,
      projects,
      departments
    } = receivablePayment ? receivablePayment : [];
    const { user } = this.props;
    let { permissions, default_employee_location } = user ? user : [];
    permissions = permissions ? permissions : [];
    default_employee_location = default_employee_location
      ? default_employee_location
      : [];
    const {
      loading,
      loadingSubmit,
      loadingInvoices,
      loadingTransactions,
      loadingForeign,
      loadingTable
    } = receivablePayment ? receivablePayment : [];

    const base_currency = currencies
      ? currencies.find(curr => curr.is_base === 1)
      : { id_currency: "01" };

    // const data = receivablePayment.receivablePayment ? receivablePayment.receivablePayment : [];

    // const used = data.is_used;
    // const error = receivablePayment
    //   ? receivablePayment.errorField
    //     ? receivablePayment.errorField
    //     : []
    //   : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <>
        <ModalAddCashBank
          handleModal={this.props.handleModalAddCashBank}
          cashBank={this.props.cashBank}
          receivablePayment={this.props.receivablePayment}
          id_location={getFieldValue("id_location")}
          submit={this.props.handleSubmitCashBank}
          visible={this.props.visibleAddCashBank}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <ModalTransaction
            visible={visible}
            receivablePayment={receivablePayment}
            handleModal={this.handleModal}
            handleSelectModal={this.handleSelectModal}
            id_location={getFieldValue("id_location")}
            id_customer={getFieldValue("id_customer")}
            transaction_date={
              getFieldValue("transaction_date")
                ? moment(getFieldValue("transaction_date"))
                    .endOf("day")
                    .format("YYYY-MM-DD HH:mm:ss")
                : moment()
                    .endOf("day")
                    .format("YYYY-MM-DD HH:mm:ss")
            }
          />

          <Tabs defaultActiveKey="1">
            <TabPane
              tab={<IntlMessages id="receivablePayments.tabs.detail" />}
              key="1"
            >
              <Row>
                <Col span={12} style={{ paddingRight: "15px" }}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.receivablePaymentNo" />
                        }
                      >
                        {getFieldDecorator("receivable_payment_no", {
                          // initialValue: data.receivable_payment_no,
                        })(<Input disabled={true} />)}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.transactionDate" />
                        }
                        style={{ marginBottom: 0, paddingBottom: 0 }}
                      >
                        {getFieldDecorator("transaction_date", {
                          initialValue: moment()
                        })(
                          <DatePicker
                            style={{ width: "100%" }}
                            showTime
                            format="DD MMMM YYYY HH:mm:ss"
                            onOpenChange={this.handleChangeTransDate}
                            disabled={
                              !permissions.includes("Can Back Date")
                                ? true
                                : false
                            }
                            allowClear={false}
                            disabledDate={e => e.valueOf() > moment()}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>

                  <Col span={24}>
                    <InputGroup>
                      <Col span={16}>
                        <Form.Item
                          {...formItemLayout}
                          label={
                            <IntlMessages id="receivablePayments.location" />
                          }
                        >
                          {getFieldDecorator("id_location", {
                            initialValue: default_employee_location.id_location,
                            rules: [
                              {
                                required: true,
                                message: <IntlMessages id="required" />
                              }
                            ]
                          })(
                            <Select
                              showSearch
                              style={{ width: "100%" }}
                              placeholder={
                                <IntlMessages id="receivablePayments.location" />
                              }
                              loading={loadingForeign}
                              onSelect={this.handleSelectLocation}
                              optionFilterProp="children"
                              filterOption={true}
                            >
                              {locations.map((data, key) => (
                                <Option key={key} value={data.id_location}>
                                  {`${data.location_code} (${data.location_name})`}
                                </Option>
                              ))}
                            </Select>
                          )}
                        </Form.Item>
                      </Col>
                      <Col span={8}>
                        <Form.Item
                          {...formItemLayout}
                          label={
                            <Tooltip
                              placement="top"
                              title={
                                <IntlMessages id="receivablePayments.importSI.help" />
                              }
                            >
                              <IntlMessages id="receivablePayments.importSI" />
                              <Icon
                                style={{ marginLeft: "5px" }}
                                type="question-circle"
                                theme="filled"
                              />
                            </Tooltip>
                          }
                        >
                          <Button
                            icon="select"
                            // loading={loadingTransactions}
                            disabled={
                              loadingTransactions ||
                              !getFieldValue("id_location") ||
                              !getFieldValue("id_customer")
                            }
                            onClick={this.handleModal}
                            style={{ height: "31px", width: "100%" }}
                            // shape="circle"
                          >
                            <IntlMessages id="button.import" />
                          </Button>
                        </Form.Item>
                      </Col>
                    </InputGroup>
                  </Col>
                </Col>
                <Col span={12} style={{ paddingLeft: "15px" }}>
                  <Form.Item
                    {...formTextArea}
                    label={<IntlMessages id="receivablePayments.note" />}
                  >
                    {getFieldDecorator("note", {
                      // initialValue: data.note
                    })(<TextArea autosize={{ minRows: 4 }} />)}
                  </Form.Item>
                </Col>
              </Row>
              <Row>
                <Col span={12} style={{ paddingRight: "15px" }}>
                  <InputGroup>
                    <Col span={16}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.customer" />
                        }
                      >
                        {getFieldDecorator("id_customer", {
                          // initialValue: data.id_customer,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="receivablePayments.customer" />
                            }
                            loading={loadingForeign}
                            onSelect={this.handleChangeCustomer}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {customers.map((data, key) => (
                              <Option key={key} value={data.id_customer}>
                                {`${data.customer_code} (${data.customer_name})`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={8}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.creditLimit" />
                        }
                      >
                        {getFieldDecorator("customer_credit_limit", {
                          // initialValue: data.customer_credit_limit
                        })(
                          <Input
                            disabled={true}
                            //   style={{ textTransform: "uppercase" }}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
                <Col span={12} style={{ paddingLeft: "15px" }} />
              </Row>

              <Divider orientation="left" style={{ fontSize: 14 }}>
                <IntlMessages id="receivablePayments.subtitle.invoice" />
              </Divider>

              <Row style={{ marginBottom: "40px" }}>
                <Col span={24}>
                  <EditableTable
                    receivablePayment={receivablePayment}
                    id_location={getFieldValue("id_location")}
                    id_customer={getFieldValue("id_customer")}
                    currency_code={this.state.currency_code}
                    invoice={this.state.invoice}
                    invoices={this.state.invoices}
                    status={1}
                    handleSaveInvoiceList={this.handleSaveInvoiceList.bind(
                      this
                    )}
                    handleSavePaymentList={this.handleSavePaymentList}
                    clearInvoice={this.clearInvoice}
                    clearInvoices={this.clearInvoices}
                    loadingTransactions={loadingTransactions}
                    loadingInvoices={loadingInvoices}
                    loadingTable={loadingTable}
                    clear={this.state.clear}
                    permissions={permissions}
                    accounts={accounts}
                    departments={departments}
                    projects={projects}
                    paymentCategories={paymentCategories}
                    errPaymentList={this.state.errPaymentList}
                  />
                </Col>
              </Row>
            </TabPane>
            <TabPane
              tab={<IntlMessages id="receivablePayments.tabs.payment" />}
              key="2"
            >
              <Row>
                <Col span={12} style={{ paddingRight: "15px" }}>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.cashBank" />
                        }
                      >
                        {getFieldDecorator("id_cash_bank", {
                          // initialValue: data.id_cash_bank,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="receivablePayments.cashBank" />
                            }
                            loading={loadingForeign}
                            notFoundContent={
                              getFieldValue("id_location") ? (
                                <Empty
                                  description={<IntlMessages id="noData" />}
                                />
                              ) : (
                                <Empty
                                  description={<IntlMessages id="noLocation" />}
                                />
                              )
                            }
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {getFieldValue("id_location") &&
                            permissions.includes("Create Cash Bank") ? (
                              <Option
                                key={"Create Cash Bank"}
                                value={undefined}
                                onClick={() =>
                                  this.props.handleModalAddCashBank()
                                }
                              >
                                <div
                                  style={{
                                    color: "#1890FF",
                                    fontStyle: "italic"
                                  }}
                                >
                                  {" + " +
                                    getLanguage({
                                      id: "cashBanks.pageHeader"
                                    })}
                                </div>
                              </Option>
                            ) : null}

                            {cashBanks.map((data, key) =>
                              data.id_location ===
                                getFieldValue("id_location") &&
                              (data.id_currency === base_currency.id_currency ||
                                data.id_currency ===
                                  getFieldValue("id_currency")) ? (
                                <Option key={key} value={data.id_cash_bank}>
                                  {data.bank_account_number === null
                                    ? data.cash_bank_code
                                    : data.cash_bank_code +
                                      " (" +
                                      data.bank_account_number +
                                      ")"}
                                </Option>
                              ) : null
                            )}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.cashFlowType" />
                        }
                      >
                        {getFieldDecorator("id_cash_flow_type", {
                          initialValue: cashFlowTypes.find(
                            x => x.default_receivable_payment === 1
                          )
                            ? cashFlowTypes.find(
                                x => x.default_receivable_payment === 1
                              ).id_cash_flow_type
                            : null,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            placeholder={
                              <IntlMessages id="receivablePayments.cashFlowType" />
                            }
                            loading={loadingForeign}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {cashFlowTypes.map((data, key) => (
                              <Option key={key} value={data.id_cash_flow_type}>
                                {`${data.cash_flow_type_code} (${data.description})`}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>

                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.referenceNo" />
                        }
                      >
                        {getFieldDecorator("reference_no", {
                          // initialValue: data.reference_no,
                        })(
                          <Input
                            placeholder={getLanguage({ id: "referenceNo" })}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.issuerDest" />
                        }
                      >
                        {getFieldDecorator("bank_issuer", {
                          // initialValue: data.bank_issuer,
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <Input
                            placeholder={getLanguage({
                              id: "issuerDestination"
                            })}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
                <Col span={12} style={{ paddingLeft: "15px" }}>
                  <InputGroup>
                    <Col span={6}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <IntlMessages id="receivablePayments.currency" />
                        }
                      >
                        {getFieldDecorator("id_currency", {
                          // initialValue: data.id_currency
                        })(
                          <Select
                            showSearch
                            style={{ width: "100%" }}
                            loading={loadingForeign}
                            disabled={true}
                            optionFilterProp="children"
                            filterOption={true}
                          >
                            {currencies.map((data, key) => (
                              <Option key={key} value={data.id_currency}>
                                {data.currency_code}
                              </Option>
                            ))}
                          </Select>
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={9}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="receivablePayments.currencyRate.help" />
                            }
                          >
                            <IntlMessages id="receivablePayments.currencyRate" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        {getFieldDecorator("currency_rate", {
                          // initialValue: data.currency_rate
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            style={{ width: "100%" }}
                            disabled={true}
                          />
                        )}
                      </Form.Item>
                    </Col>
                    <Col span={9}>
                      <Form.Item
                        {...formItemLayout}
                        label={
                          <Tooltip
                            placement="top"
                            title={
                              <IntlMessages id="receivablePayments.fiscalRate.help" />
                            }
                          >
                            <IntlMessages id="receivablePayments.fiscalRate" />
                            <Icon
                              style={{ marginLeft: "5px" }}
                              type="question-circle"
                              theme="filled"
                            />
                          </Tooltip>
                        }
                      >
                        {getFieldDecorator("fiscal_rate", {
                          // initialValue: data.fiscal_rate
                        })(
                          <InputNumber
                            formatter={value =>
                              `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                            }
                            parser={value => value.replace(/\$\s?|(,*)/g, "")}
                            min={0}
                            style={{ width: "100%" }}
                            disabled={true}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                  <InputGroup>
                    <Col span={12}>
                      <Form.Item
                        {...formItemLayout}
                        label={<IntlMessages id="receivablePayments.dueDate" />}
                      >
                        {getFieldDecorator("due_date", {
                          initialValue: getFieldValue("transaction_date"),
                          rules: [
                            {
                              required: true,
                              message: <IntlMessages id="required" />
                            }
                          ]
                        })(
                          <DatePicker
                            style={{ width: "100%" }}
                            showTime
                            format="DD MMMM YYYY HH:mm:ss"
                            allowClear={false}
                            disabledDate={e =>
                              e.valueOf() <= getFieldValue("transaction_date")
                            }
                            disabled={true}
                          />
                        )}
                      </Form.Item>
                    </Col>
                  </InputGroup>
                </Col>
              </Row>
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="3"
              disabled
            />
          </Tabs>

          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginTop: "24px" }}
              loading={loading || loadingSubmit}
              disabled={!permissions.includes("Create Receivable Payment")}
              onClick={() => removeSaveData("pp")}
            >
              <IntlMessages id="button.submit" />
            </Button>

            <br />

            {this.state.err ? (
              <span style={{ color: "#f5222d" }}>
                <IntlMessages id="form.error" />
              </span>
            ) : null}

            {this.state.err_invoice ? (
              <IntlMessages id="invoiceDataEmpty">
                {txt => <span style={{ color: "#f5222d" }}>{txt}</span>}
              </IntlMessages>
            ) : null}
          </Form.Item>
        </Form>
      </>
    );
  }
}

const WrappedReceivablePaymentCreateForm = Form.create({
  name: "create_receivable_payment"
})(ReceivablePaymentCreateForm);

const mapStateToProps = state => {
  return {
    user: state.auth.user,
    auth: state.auth
  };
};

export default compose(
  connect(mapStateToProps, { getTransactions, getDiscounts })
)(WrappedReceivablePaymentCreateForm);
