import React from "react";
import PropTypes from "prop-types";
import NumberFormat from "../../../App/NumberFormat";

export default class TotalMemo extends React.Component {
  static propTypes = {
    /** set error atau tidak */
    hasError: PropTypes.bool,
    /** total yang akan ditampilkan */
    value: PropTypes.number,
    /** pesan error yang akan ditampilkan jika ada error */
    message: PropTypes.node
  };

  render() {
    const props = this.props;
    return props.hasError ? (
      <div style={{ textAlign: "left" }}>
        {props.children}
        {" : "}
        <strong style={{ color: "#f5222d", fontSize: "22px" }}>
          <NumberFormat value={props.value} />
        </strong>
        <br />
        <span style={{ color: "#f5222d" }}>{props.message}</span>
      </div>
    ) : (
      <div style={{ textAlign: "left" }}>
        {props.children} {" : "}
        <strong style={{ fontSize: "22px" }}>
          <NumberFormat value={props.value} />
        </strong>
      </div>
    );
  }
}
