import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
// import Button from "../../../../components/uielements/button";
import {
  Alert,
  Button,
  Col,
  Empty,
  Form,
  // InputNumber,
  Modal,
  Row,
  Select,
  Spin
  // Radio,
  // Select
} from "antd";
import EditableTableMultiPayment from "./editableTableMultiPayment";
import { Span, Strong } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";

const { Option } = Select;

// const { Option } = Select;
class ModalMultiPayment extends Component {
  state = {
    outstanding: this.props.outstanding,
    dataSource: [],
    includeInTotal: false,
    limitInitPayCat: 0,
    limitInitTable: 0,
    totalAmount: this.props.totalAmount,
    paidAmount: this.props.paidAmount,
    paymentCategories: [],
    paymentSelectors: [],
    changed: false
  };

  componentDidMount() {}
  componentDidUpdate(prevProps, prevState) {
    const { paymentCategories, totalAmount, multiPaymentDetails } = this.props;
    const {
      limitInitPayCat,
      limitInitTable,
      // paidAmount,
      paymentSelectors
    } = this.state;
    // if dataSource still empty
    if (
      paymentSelectors.length === 0 &&
      paymentCategories.length > 0 &&
      limitInitPayCat === 0
    ) {
      let paymentSelectors = [];
      paymentCategories.map((data, key) =>
        data.payment_type === 1 || data.payment_type === 3
          ? paymentSelectors.push({ ...data, key })
          : null
      );

      this.setState({
        paymentSelectors,
        limitInitPayCat: 1
      });
    }
    if (
      Array.isArray(multiPaymentDetails) &&
      multiPaymentDetails.length > 0 &&
      totalAmount > 0 &&
      limitInitTable === 0
    ) {
      let initData = [];
      let newPaidAmount = 0;
      let outstanding = 0;
      multiPaymentDetails.map(data => {
        const payment = Object.assign({}, data, {
          key: data.index_no,
          payment_category_code: data.payment_category.payment_category_code,
          payment_amount: parseFloat(data.payment_amount)
        });
        initData.push(payment);
        newPaidAmount += payment.payment_amount;
      });
      outstanding = newPaidAmount - totalAmount;

      this.setState({
        dataSource: initData,
        paidAmount: newPaidAmount,
        outstanding,
        limitInitTable: 1
      }, () => this.handleSubmit());
    }
    // if paymentType is equal to Cash
    // if (prevProps.idPaymentCategory !== idPaymentCategory) {
    //   if (paymentType === 1) {
    //     const paymentCat = paymentCategories.filter(
    //       data => data.id_payment_category === idPaymentCategory
    //     );
    //     this.setState({
    //       outstanding: 0,
    //       dataSource: paymentCat,
    //       paidAmount: 0
    //     });
    //   }
    // }
    // if (prevProps.totalAmount !== totalAmount) {
    //   var outstanding = paidAmount - totalAmount;
    //   if (outstanding < 0) {
    //     outstanding = 0;
    //   }
    //   this.setState({
    //     outstanding,
    //     totalAmount
    //   });
    // }

    // reset ketika ada perubahan payment amount, diskon, dan DP/DM
    if (
      ((prevProps.totalPaymentAmount !== this.props.totalPaymentAmount && prevProps.totalPaymentAmount) ||
      (prevProps.totalDPDM !== this.props.totalDPDM && prevProps.totalDPDM) ||
      (prevProps.totalDiscount !== this.props.totalDiscount && prevProps.totalDiscount)) &&
      this.state.dataSource.length > 0
    ) {
      this.handleReset()
    }
  }

  handleIncludeFreightCostinTotal = e => {
    this.setState({ includeInTotal: e.target.checked });
  };

  handleReset = () => {
    this.setState({
      outstanding: 0,
      dataSource: [],
      paidAmount: 0
    });

    const data = {
      receivable_payment_categories: [],
      outstanding: 0,
      paidAmount: 0
    };
    this.props.submit(data);
  };

  handleSaveMultiPaymentList = (dataSource, paidAmount) => {
    const { totalAmount } = this.props;
    const outstanding = paidAmount - totalAmount;

    // console.log("dataSource", dataSource);

    this.setState({ outstanding, dataSource, paidAmount, changed: true });
  };

  handleSelectPayment = value => {
    const { dataSource, paymentSelectors, paidAmount } = this.state;
    const { totalAmount } = this.props;
    let paymentAmount = 0;

    if (dataSource.length > 0) {
      const find = dataSource.findIndex(
        data => data.id_payment_category === value
      );
      if (find < 0) {
        const paymentSelector = paymentSelectors.find(
          data => data.id_payment_category === value
        );
        paymentAmount = parseFloat(totalAmount) - parseFloat(paidAmount);
        const newPaidAmount = paidAmount + paymentAmount;
        const outstanding = newPaidAmount - totalAmount;
        const init = {
          key: paymentSelector.key,
          id_payment_category: paymentSelector.id_payment_category,
          payment_category_code: paymentSelector.payment_category_code,
          payment_type: paymentSelector.payment_type,
          total_amount: parseFloat(totalAmount),
          payment_amount: paymentAmount,
          name_on_card: null,
          bank_issuer: "-",
          card_no: null,
          reference_no: "-"
        };
        dataSource.push(init);
        this.setState({
          dataSource,
          paidAmount: newPaidAmount,
          outstanding,
          changed: true
        });
      }
    } else {
      const paymentSelector = paymentSelectors.find(
        data => data.id_payment_category === value
      );
      paymentAmount = parseFloat(totalAmount) - parseFloat(paidAmount);
      const newPaidAmount = paidAmount + paymentAmount;
      const outstanding = newPaidAmount - totalAmount;
      const init = {
        key: paymentSelector.key,
        id_payment_category: paymentSelector.id_payment_category,
        payment_category_code: paymentSelector.payment_category_code,
        payment_type: paymentSelector.payment_type,
        total_amount: parseFloat(totalAmount),
        payment_amount: paymentAmount,
        name_on_card: null,
        bank_issuer: "-",
        card_no: null,
        reference_no: "-"
      };
      dataSource.push(init);
      this.setState({
        dataSource,
        paidAmount: newPaidAmount,
        outstanding,
        changed: true
      });
    }
  };

  handleSubmit = e => {
    // e.preventDefault();
    const { outstanding, dataSource, paidAmount } = this.state;
    let multiPayments = {
      receivable_payment_categories: dataSource.filter(
        data => data.payment_amount !== 0
      )
    };

    // masukan sisa pembayaran ke payment method yang terakhir
    const length = multiPayments.receivable_payment_categories.length
    if (length > 0) {
      multiPayments.receivable_payment_categories[length - 1].payment_amount -= outstanding
    }

    const data = Object.assign({}, multiPayments, {
      outstanding: 0,
      paidAmount: paidAmount - outstanding
    });
    this.setState({
      dataSource: multiPayments.receivable_payment_categories,
      outstanding: 0,
      paidAmount: paidAmount - outstanding,
      changed: false
    });
    this.props.submit(data);
  };

  render() {
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const { loadingForeign, totalAmount } = this.props;
    const {
      outstanding,
      paidAmount,
      paymentSelectors
    } = this.state;
    // console.log("dataSource ", dataSource);

    return (
      <Modal
        centered
        closable={false}
        footer={
          <Row justify="end" type="flex">
            <Col>
              <Button
                onClick={() => {
                  this.props.handleModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>

              <Button
                onClick={() => {
                  this.handleReset();
                }}
                disabled={this.props.status !== 1}
              >
                <IntlMessages id="filter.reset" />
              </Button>

              <Button
                type="primary"
                onClick={this.handleSubmit}
                disabled={
                  parseFloat(parseFloat(this.state.outstanding).toFixed(2)) !==
                    0 || this.props.status !== 1
                }
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
        maskClosable={false}
        title={<IntlMessages id="salesInvoices.setPaidAmount" />}
        visible={this.props.visible}
        // onCancel={() => {
        //   if (transactionStatus === 4 || transactionStatus === 6) {
        //     const { resetFields } = this.props.form;
        //     resetFields();
        //   }
        //   this.props.handleModal();
        // }}
        // onOk={this.handleSubmit}
        width="50%"
      >
        <Row>
          <Col span={24}>
            <Form.Item {...formItemLayout}>
              <Select
                allowClear
                filterOption={false}
                placeholder={
                  <IntlMessages id="paymentCategories.button.select" />
                }
                loading={loadingForeign}
                notFoundContent={
                  loadingForeign ? (
                    <Spin size="small" />
                  ) : (
                    <Empty
                      description={
                        <IntlMessages id="paymentCategories.table.noData" />
                      }
                    />
                  )
                }
                onSelect={this.handleSelectPayment}
                onSearch={this.handleSearchPayment}
                optionFilterProp="children"
                style={{ width: "100%" }}
                filterOption={true}
                value={undefined}
                disabled={this.props.status !== 1}
              >
                {paymentSelectors && paymentSelectors.length > 0
                  ? paymentSelectors.map(data => (
                      <Option
                        key={data.id_payment_category}
                        value={data.id_payment_category}
                      >
                        {data.payment_category_code}
                      </Option>
                    ))
                  : null}
              </Select>
            </Form.Item>
          </Col>
        </Row>

        {this.state.changed ? (
          <Alert
            message={<IntlMessages id="salesInvoices.saveMultiPayment" />}
            type="warning"
            showIcon
            style={{ marginBottom: "10px" }}
          />
        ) : (
          ""
        )}
        <Form layout="vertical">
          <EditableTableMultiPayment
            // {...formItemLayout}
            dataSource={this.state.dataSource}
            handleSaveMultiPaymentList={this.handleSaveMultiPaymentList}
            totalAmount={totalAmount}
            status={this.props.status}
          />
          <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
            <Col span={15} style={{ marginTop: "25px", textAlign: "right" }}>
              <strong>
                <IntlMessages id="paidAmount" />
              </strong>
            </Col>
            <Col span={8} offset={1} style={{ marginTop: "25px" }}>
              <span>
                <NumberFormat value={paidAmount} />
              </span>
            </Col>
          </Row>
          <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
            <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
              <strong>
                <IntlMessages id="totalAmount" />
              </strong>
            </Col>
            <Col span={8} offset={1} style={{ marginTop: "5px" }}>
              <span>
                <NumberFormat value={totalAmount} />
              </span>
            </Col>
          </Row>
          <Row type="flex" justify="end" style={{ marginBottom: "6px" }}>
            <Col span={15} style={{ marginTop: "5px", textAlign: "right" }}>
              <Strong
                error={
                  parseFloat(parseFloat(this.state.outstanding).toFixed(2)) !==
                  0
                }
              >
                {parseFloat(this.state.outstanding) < 0 
                  ? (<IntlMessages id="outstanding" />)
                  : (<IntlMessages id="overPayment" />)}
              </Strong>
            </Col>
            <Col span={8} offset={1} style={{ marginTop: "5px" }}>
              <Span
                error={
                  parseFloat(parseFloat(this.state.outstanding).toFixed(2)) !==
                  0
                }
              >
                {Math.abs(parseFloat(outstanding))
                  .toFixed(2)
                  .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
              </Span>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalMultiPayment = Form.create({ name: "multipayment_modal" })(
  ModalMultiPayment
);

export default WrappedModalMultiPayment;
