import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/financial/receivablePayment/actions";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import NumberFormat from "../../../App/NumberFormat";

import { Modal, Tooltip, Table, Row, Col, Empty } from "antd";
import TotalMemo from "./totalMemo";
import getLanguage from "../../../App/MyLanguange";

const { getCreditMemos } = actions;

class ModalCreditMemo extends Component {
  state = {
    filter: {
      id_customer: null,
      id_location: null,
      status: 2,
      payment_status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null,
    selectedRowKeys: [],
    selectedRows: [],
    totalCreditMemo: 0
  };

  componentDidMount() {
    const { id_location, id_customer } = this.props;

    if (id_location && id_customer) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_customer: id_customer,
          id_location: id_location
        }
      });
      this.handleLocationCustomerChange(id_location, id_customer);
    }
  }

  componentDidUpdate(prevProps) {
    const { id_location, id_customer, selectedData } = this.props;

    if (prevProps.selectedData !== selectedData && selectedData.detail_memos) {
      let selectedRowKeys = [];
      let selectedRows = [];
      let newTotDM = 0;
      selectedData.detail_memos.map(data => {
        selectedRowKeys.push(data.id_credit_memo);
        selectedRows.push(data);
        newTotDM += data.memo_amount
          ? parseFloat(data.memo_amount)
          : parseFloat(data.currency_amount);
      });
      // console.log(selectedRowKeys, selectedRows);
      this.setState({
        selectedRowKeys: selectedRowKeys,
        selectedRows: selectedRows,
        totalCreditMemo: newTotDM
      });
    }

    if (
      prevProps.id_location !== id_location &&
      prevProps.id_customer !== id_customer &&
      id_location &&
      id_customer
    ) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_customer: id_customer,
          id_location: id_location
        }
      });
      this.handleLocationCustomerChange(id_location, id_customer);
    } else if (prevProps.id_location !== id_location && id_customer) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_customer: id_customer,
          id_location: id_location
        }
      });
      this.handleLocationCustomerChange(id_location, id_customer);
    } else if (prevProps.id_customer !== id_customer && id_location) {
      this.setState({
        filter: {
          ...this.state.filter,
          id_customer: id_customer,
          id_location: id_location
        }
      });
      this.handleLocationCustomerChange(id_location, id_customer);
    }
  }

  handleLocationCustomerChange = (id_location, id_customer) => {
    const { getCreditMemos } = this.props;
    getCreditMemos(id_location, id_customer);
  };

  handleLocationChange = id_location => {
    const { getCreditMemos, id_customer } = this.props;
    if (id_customer) {
      getCreditMemos(id_location, id_customer);
    }
  };

  handleCustomerChange = id_customer => {
    const { getCreditMemos, id_location } = this.props;
    if (id_location) {
      getCreditMemos(id_location, id_customer);
    }
  };

  handleSubmit = () => {
    const { selectedData } = this.props;
    const { selectedRowKeys, selectedRows } = this.state;
    const creditMemos = {
      detail_memos: this.state.selectedRows
    };
    const deleteKeys = selectedData.detail_memos.map(
      data => data.id_credit_memo
    );
    const data = Object.assign({}, selectedData, creditMemos);
    // console.log(selectedRowKeys, selectedRows);
    this.props.handleOkModal(data, selectedRowKeys, selectedRows, deleteKeys);
  };

  rowSelection = () => {
    return {
      onChange: (selectedRowKeys, selectedRows) => {
        let total = 0;
        selectedRows.map(data => {
          total += data.currency_amount
            ? parseFloat(data.currency_amount)
            : parseFloat(data.memo_amount);
        });
        this.setState({
          selectedRowKeys: selectedRowKeys,
          selectedRows: selectedRows,
          totalCreditMemo: total
        });
      },
      getCheckboxProps: record => ({
        disabled: record.id_payment_invoice
        // name: record.name,
      }),
      selectedRowKeys: this.state.selectedRowKeys,
      hideDefaultSelections: true
    };
  };

  render() {
    // console.log(this.props);

    const {
      visible,
      handleModal,
      receivablePayment,
      selectedData
    } = this.props;
    const { totalCreditMemo } = this.state;
    const { creditMemos } = receivablePayment ? receivablePayment : [];
    const { loadingCreditMemos } = receivablePayment
      ? receivablePayment
      : false;

    const columns = [
      {
        title: getLanguage({id: "transactionNo"}),
        dataIndex: "credit_memo_no",
        rowKey: "credit_memo_no",
        width: "20%"
      },
      {
        title: getLanguage({id: "paymentTransaction"}),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "20%",
        render: text => {
          if (text) {
            return text;
          } else {
            return "-";
          }
        }
      },
      {
        title: getLanguage({ id: "date"}),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "amount" }),
        dataIndex: "memo_amount",
        rowKey: "memo_amount",
        width: "20%",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const dataSource = [];
    Object.keys(creditMemos).map((item, index) => {
      if (creditMemos.length > 0) {
        creditMemos[item].memo_amount = creditMemos[item].memo_amount
          ? parseFloat(creditMemos[item].memo_amount)
          : parseFloat(creditMemos[item].currency_amount);
      }
      const find = this.props.selectedRowKeys.findIndex(id => {
        return id === creditMemos[item].id_credit_memo;
      });
      // const memoAmount = creditMemos[item].memo_amount
      //   ? parseFloat(creditMemos[item].memo_amount)
      //   : parseFloat(creditMemos[item].currency_amount);
      // const subTotal = parseFloat(selectedData.sub_total) + selectedData.sub_total_memo_amount -
      // selectedData.sub_total_discount_amount;
      if (selectedData.sales_invoice_amount) {
        // cek dari ST atau bukan
        if (find < 0 && (
          !creditMemos[item].id_payment_invoice 
          // || creditMemos[item].id_payment_invoice === selectedData.id_sales_invoice
        )) {
          return dataSource.push({
            ...creditMemos[item],
            key: item
          });
        }
      } else {
        return dataSource.push({
          ...creditMemos[item],
          key: item
        });
      }
    });

    let modifiedData = [];
    if (dataSource.length === 0 && Array.isArray(selectedData.detail_memos)) {
      modifiedData = selectedData.detail_memos;
    } else if (
      Array.isArray(selectedData.detail_memos) &&
      dataSource.length > 0
    ) {
      modifiedData = [...selectedData.detail_memos, ...dataSource];
    } else {
      modifiedData = dataSource;
    }

    // console.log(modifiedData);

    return (
      <Modal
        title={<IntlMessages id="receivablePayments.importCreditMemo" />}
        visible={visible}
        centered
        footer={
          <Row justify="space-between" type="flex">
            <Col>
              {/* <IntlMessages id="receivablePayments.totalCreditMemo" /> :{" "}
              <strong style={{ fontSize: "22px" }}>
                {totalCreditMemo
                  .toFixed(2)
                  .replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
              </strong> */}
              <TotalMemo
                hasError={
                  totalCreditMemo >
                  selectedData.sales_invoice_amount -
                    selectedData.paid_amount -
                    selectedData.sub_total_discount_amount
                }
                message={
                  totalCreditMemo >
                  selectedData.sales_invoice_amount -
                    selectedData.paid_amount -
                    selectedData.sub_total_discount_amount ? (
                    <IntlMessages id="receivablePayments.totalCreditMemo.message" />
                  ) : null
                }
                value={totalCreditMemo}
              >
                <IntlMessages id="receivablePayments.totalCreditMemo" />
              </TotalMemo>
            </Col>
            <Col>
              <Button
                onClick={() => {
                  // this.setState({
                  //   selectedRowKeys: [],
                  //   selectedRows: []
                  // });
                  handleModal();
                }}
              >
                <IntlMessages id="button.cancel" />
              </Button>
              <Button
                type="primary"
                onClick={() => {
                  this.handleSubmit();
                }}
                disabled={
                  totalCreditMemo >
                    selectedData.sales_invoice_amount -
                      selectedData.paid_amount -
                      selectedData.sub_total_discount_amount ||
                  this.props.status !== 1
                }
              >
                <IntlMessages id="button.ok" />
              </Button>
            </Col>
          </Row>
        }
        keyboard={true}
        onCancel={() => {
          // this.setState({
          //   selectedRowKeys: [],
          //   selectedRows: []
          // });
          handleModal();
        }}
        width="95%"
      >
        <Row style={{ height: "450px" }}>
          <Col>
            {modifiedData.length === 0 && !loadingCreditMemos ? (
              <Empty description={<IntlMessages id="noData" />} />
            ) : (
              <Table
                size="small"
                rowKey={record => record.id_credit_memo}
                scroll={{ y: 400 }}
                rowSelection={this.rowSelection()}
                dataSource={modifiedData}
                columns={columns}
                loading={loadingCreditMemos}
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getCreditMemos }
  )
)(ModalCreditMemo);
