import React, { Component } from "react";
// import moment from "moment";

import { Button, Form, Popconfirm, Tooltip } from "antd";
import EditableCell from "./editableCellMultiPayment";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import CardWrapper from "../../../App/app.style";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { dataSource } = props;

    this.state = {
      dataSource: [...dataSource],
      count: dataSource.length + 1,
      limit: 0,
      totalAmount: 0
    };
  }

  componentDidMount() {}

  componentDidUpdate(prevProps) {
    // console.log("updated, prev-> ", this.prevProps.salesOrders);
    const { dataSource } = this.props;
    const { limit } = this.state;
    if (prevProps.dataSource === dataSource && limit === 0) {
      this.setState({
        dataSource,
        limit: 1
      });
    } else if (this.state.dataSource !== dataSource) {
      this.setState({
        dataSource
      });
    }
  }

  handleDelete = key => {
    const { handleSaveMultiPaymentList } = this.props;
    let newPaidAmount = 0;

    const dataSource = this.state.dataSource;

    const deleted = dataSource.find(currData => currData.key === key);
    // console.log("yang dihapus ", deleted);
    let newData = null;
    if (deleted.id_payment_category) {
      newData = dataSource.filter(
        currData =>
          currData.key !== key &&
          currData.id_payment_category !== deleted.id_payment_category
      );
    } else {
      newData = dataSource.filter(currData => currData.key !== key);
    }
    // console.log("hasil newData", newData);

    newData.map((currData, index) => {
      currData.key = index + 1;
      newPaidAmount += parseFloat(currData.payment_amount);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalQty: newPaidAmount
    });
    // console.log("deleted and the new data is", newData);
    handleSaveMultiPaymentList(newData, newPaidAmount);
  };

  handleSave = row => {
    const { handleSaveMultiPaymentList } = this.props;
    const newData = this.state.dataSource;

    const index = newData.findIndex(currData => row.key === currData.key);

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    let newPaidAmount = 0;

    newData.map(currData => {
      if (currData.payment_amount) {
        newPaidAmount += parseFloat(currData.payment_amount);
      }
    });

    this.setState({
      dataSource: [...newData]
    });

    // console.log("new Datas", newData);
    handleSaveMultiPaymentList(newData, newPaidAmount);
  };

  render() {
    // console.log(this.state.dataSource);
    // const { totalAmount } = this.props;
    const { dataSource } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const raw_columns = [
      {
        title: getLanguage({ id: "paymentCategoryCode" }),
        dataIndex: "payment_category_code",
        rowKey: "payment_type",
        width: "15%",
      },
      {
        title: getLanguage({ id: "type" }),
        dataIndex: "payment_type",
        rowKey: "payment_type",
        width: "10%",
        render: text => {
          if (text === 1) {
            return getLanguage({ id: "cash" });
          } else if (text === 2) {
            return getLanguage({ id: "credit" });
          } else if (text === 3) {
            return getLanguage({ id: "card" });
          } else if (text === 4) {
            return getLanguage({ id: "multi" });
          }
        }
      },
      {
        title: getLanguage({ id: "paymentAmount" }),
        dataIndex: "payment_amount",
        rowKey: "payment_amount",
        width: "15%",
        editable: true,
        render: text => <NumberFormat value={text} />
      },
      {
        title: "",
        dataIndex: "operation",
        width: "10%",
        align: "center",
        render: (text, row) => {
          if (1 === 1) {
            //checking transaction status, will be fixed soon
            return (
              <div>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  onConfirm={() => this.handleDelete(row.key)}
                  okText={<IntlMessages id="yes" />}
                  cancelText={<IntlMessages id="no" />}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          }
        }
      }
    ];

    const columns = raw_columns.map(col => {
      if (!col.editable) {
        return col;
      } else {
        return {
          ...col,
          onCell: record => ({
            record,
            editable: this.props.status !== 1 ? false : col.editable,
            dataIndex: col.dataIndex,
            title: col.title,
            // status: this.props.status,
            handleSave: this.handleSave
          })
        };
      }
    });

    // const info = [
    //   {
    //     payment_amount: paidAmount,
    //     key: dataSource.length > 0 ? dataSource.length + 1 : "",
    //     payment_category_code: "Paid Amount"
    //   },
    //   {
    //     payment_amount: totalAmount,
    //     key: dataSource.length > 0 ? dataSource.length + 2 : "",
    //     payment_category_code: "Total Amount"
    //   },
    //   {
    //     payment_amount: paidAmount - totalAmount < 0 ? 0 : paidAmount - totalAmount,
    //     key: dataSource.length > 0 ? dataSource.length + 3 : "",
    //     payment_category_code: "Change Amount"
    //   }
    // ];
    // const modifiedData = dataSource.length > 0 ? [...dataSource, ...info] : [];
    return (
      <CardWrapper>
        <div>
          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.id_payment_category}
              rowClassName={() => "editable-row"}
              dataSource={dataSource}
              columns={columns}
              pagination={false}
              // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper> 
    );
  }
}

export default EditableTable;
